.monitoring-content {
    padding-bottom: 150px;

    font-family: $segoe;
    color: $darktext;
}

.monitoring-content__wrapper {
    display: flex;
}

.monitoring-content__main {
    flex: 1 1 66.2%;
    margin-right: 1.3%;
}

.monitoring-content__sidebar {
    flex: 1 1 32.5%;
}

@media (max-width: 1024px) {
    .monitoring-content__wrapper {
        flex-flow: column nowrap
    }

    .monitoring-content__main {
        margin: 0;
    }

    .monitoring-content__sidebar {
        margin: 0 0 16px;
        order: -1;
    }
}

@media (max-width: 480px) {
    .monitoring-content {
        padding-bottom: 70px;
    }
}